@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

li {
  text-decoration: none;
  list-style: none;
}
a {
  text-decoration: none;
  color: white;
}
.navbar {
  position: fixed;
  width:100%;
  height: 60px;
  line-height: 12vh;
  box-shadow: rgba(50, 50, 93, 0.23) 0 30px 30px -20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to right, #4299e0, #4caf50);
  color: white;
}

.logo {
  /* font-size: 22px; */
  margin-left: 2%;
  height: 50px;
  border-radius: 50%;
}
.nav-link {
  display: flex;
  justify-content: flex-end;
  width: 75%;
}

.nav-link li {
  color: white;
  font-size: 16px;
  padding: 0 5px;
  margin: 0 10px;
  cursor: pointer;
  text-transform: capitalize;
  text-decoration: none;
}
.nav-link Link {
  text-decoration: transparent;
}
button {
  border: none;
  background-color: none;
  font-size: 20px;
  background-color: transparent;
}
.mobile-menu-icon {
  display: none;
}
.login {
  font-size: 16px;
  background-color: transparent;
}

@media (max-width: 768px) {
  .nav-link {
    display: none;
  }
  .nav-link-mobile {
    position: absolute;
    display: block;
    list-style: none;
    box-shadow: rgba(50, 50, 93, 0.23) 0 30px 30px -20px;
    left: 0;
    top: 0;
    height: 100vh;
    transition: all 0.5s ease-in-out;
    width: 100%;
    background-color: #4caf50;
    /* background: linear-gradient(to right, #4299e0, #4caf50); */
    font-size: 22px;
    z-index: 1;
  }
  ul li {
    padding: 32px;
    width: 100%;
    transition: all 0.5s ease-in-out;
    text-align: center;
  }
  .mobile-menu-icon {
    position: absolute;
    display: block;
    right: 20px;
    z-index: 2;
  }
  .login {
    font-size: 22px;
    background-color: transparent;
  }
}
@media (max-width: 620px) {
  .mainBoxFilter {
    z-index: 10;
  }
  .BlogListingPage{
    width: 100%;
  }
}